input.checkbox{
 display: none;
 &:checked + label{
  &:before {
  background: #fff;
  border: 1px solid $main-color;
 }
  &:after {
   opacity: 1;
   -webkit-transform: rotate(-45deg) scale(1);
   transform: rotate(-45deg) scale(1);
  }
 }//checked


 &+ label {
  position: relative;
  display: inline-block;
  padding: 3px 3px 3px 22px;
  cursor: pointer;
  -webkit-transition: all .2s;
  transition: all .2s;

  &:before,&:after {
   position: absolute;
   content: '';
   -webkit-transition: all .2s;
   transition: all .2s;
  }

  &:before {
   top: 50%;
   left: 0;
   width: 14px;
   height: 14px;
   margin-top: -8px;
   background: #f4f4f4;
   border: 1px solid #ccc;
   border-radius: 3px;
  }

  &:after {
   opacity: 0;
   top: 50%;
   left: 3px;
   width: 8px;
   height: 4px;
   margin-top: -4px;
   border-left: 2px solid $main-color;
   border-bottom: 2px solid $main-color;
   -webkit-transform: rotate(-45deg) scale(.5);
   transform: rotate(-45deg) scale(.5);
  }

  &:hover {
   color: $main-color;
   cursor: pointer;
   &:before {
    background: #fff;
   }
  }//hover

 }//label

}//type=checkbox
